@import "../../scss/global";

header {
    background-color: $primary-color;
    position: fixed;
    width: 100%;
  }

  .transparent-header {
    background-color: rgba(232, 216, 196, 0.2); 
    backdrop-filter: blur(10px); 
  }
  
  .header {
    @include center();
    @include flex(space-between);
    gap: 0px 50px;
  
    picture {
      display: inline-block;
      width: 80px;
      padding: 10px 10px; 
    }
  
    nav {
      @include flex(center);
      gap: 10px 50px;
  
      a {
        color: #000000;
        font-size: 1.25rem;
        font-weight: bold;
  
        &:hover {
          color: $hover;
        }
      }
    }
  }
  
  @media (max-width: 768px) {
    header {
      top: -10px;
    }
  
    .header {
      display: grid;
      justify-content: center;
  
      picture {
        margin: 0 auto;
      }
  
      nav {
        padding-bottom: 10px;
  
        &::before {
          display: inline-block;
          content: '';
          width: 100%;
          height: 1px;
          background-color: #000000;
        }
      }
    }
  }
  
  @media (max-width: 375px) {
    .header {
      nav {
        gap: 5px 20px;
      }
    }
  }
  